#hero{
    background-color: rgb(27, 10, 0);
    height: 85vh;
}

.hero-container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
    color: white;
}

.hero-left{
    position: relative;
    top: 10rem;
    padding: 0 2rem;
    
}

.hero-left h1{
    font-size: 50px;
}

.hero-left h2{
    text-transform: uppercase;
    font-size: 40px;
    
}

.hero-left p{
    margin: 1rem 0;
    font-style: italic;
}

.btn{
    font-weight: 600;
    background-color: white;
    color: rgb(27, 10, 0);
    padding: 8px 15px;
    border-radius: 5px;
    cursor: pointer;
    border: none;
    box-shadow: 0 0 30px rgba(0, 0, 0, .5);
    transition: 0.5s ease-in-out;
}

.btn:hover{
    background:transparent;
    border: 1px solid rgb(255, 255, 255);
    color: white;
    overflow: hidden;
}

.hero-stats{
    display: flex;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    margin-top: 2rem;
    width: 60%;
}

.stat-box{
    display: flex;
    flex-direction: column;
}

.stat-box>span:first-child{
    font-size: 1rem;
    font-weight: 500;
    text-transform: uppercase;
}

.stat-box span p{
    font-size: 1.5rem;
    display: inline-block;
}

.stat-box>span:last-child{
    font-size: 1rem;
    font-weight: 700;
    text-transform: uppercase;
}


.hero-right{
    
    height: 33rem;
    display: flex;
    align-items: center;
    overflow: hidden;
}

.slides{
    width: 500%;
    height: 500px;
    display: flex;
}

.slides input{
    display: none;
}

.slides img{
    width: 400px;
    height: 500px;
}

.slide{
    width: 10%;
    transition: 2s;
}







