.categories-container{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
    align-items: center;
}

.category-card{
    text-align: center;
    cursor: pointer;
    width: 200px;
    height: 200px;
}

.category-card img{
    background: linear-gradient(rgb(0, 0, 0), rgb(27, 10, 0));
    width: 200px;
    height: 200px;
    padding: 20px 20px 0 20px;
}

.category-card:hover{
    box-shadow: 0 5px 20px 5px grey;
}


